import React,{useContext,useState} from 'react'
import { AuthContext } from '../../../context/authContext'
import BaseLayout from '../../../component/shared/baseLayout';
import {InputLabel,TextField,Button,CircularProgress} from '@material-ui/core'
import { VideoUpload } from '../../../firebase/admin/videos';
import { toast } from 'react-toastify';
import NotAuthorizeAdmin from '../../../component/admin/shared/notAuthorize';
const cardContainer={width:"80%",alignSelf:"center"}
export default function VideosUpload(){
    const [auth,_]=useContext(AuthContext)
    const [isLoading,setIsLoading]=useState(false)
    const [link,setLink]=useState("")
    const [error,setError]=useState("")
    const handleSubmit=async()=>{
        setIsLoading(true)
        const res=await VideoUpload(link)
        if(res.status==200){
            toast.success(res.message)
        }else{
            setError(res.message)
        }
        setIsLoading(false)
        setLink("")
    }
    if(!auth.isAdmin){
        return(
            <NotAuthorizeAdmin/>
        );
    }
    return (
    <div>
        <BaseLayout/>
        <br/>
        <br/>
        <div style={{display:'flex',justifyContent:'center'}}>
            <div className="login-card" style={cardContainer}>
            <div className="form-group" style={{width:"95%",paddingLeft:20,paddingTop:20}}>
                <InputLabel id="demo-simple-select-label">Video Link</InputLabel>
                <TextField placeholder="enter the embed video link" style={{width:'95%'}}
                    value={link}
                    onChange={(e)=>{
                        setLink(e.target.value)
                    }}
                />
                <br/>
                {!isLoading?<Button style={{marginTop:20}} color="primary" variant="contained" 
                onClick={handleSubmit}>Upload</Button>:<CircularProgress style={{fontSize:24,color:"green",marginTop:20}}/>}
            </div>
            {error.length>0 && <p style={{color:"red",fontSize:25}}>{error}</p>}
        </div>
        </div>
    </div>
    )
}