import React,{useState,useEffect} from 'react'
import SearchIcon from '@material-ui/icons/Search';
import InputBase from '@material-ui/core/InputBase';
import Button from '@material-ui/core/Button'
import {Row} from 'reactstrap'
import { lighten, makeStyles,fade } from '@material-ui/core/styles';

const useToolbarStyles = makeStyles(theme => ({
    root: {
      paddingLeft: theme.spacing(2),
      paddingRight: theme.spacing(1),
    },
    search: {
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: fade(theme.palette.secondary.light, 0.15),
        '&:hover': {
          backgroundColor: fade(theme.palette.secondary.light, 0.25),
        },
        marginRight: theme.spacing(2),
        marginLeft: 0,
        width: '100%',
        [theme.breakpoints.up('sm')]: {
          marginLeft: theme.spacing(3),
          width: 'auto',
        },
      },
      searchIcon: {
        padding: theme.spacing(0, 2),
        height: '100%',
        position: 'absolute',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
      },
      inputRoot: {
        color: 'inherit',
        width:"100%"
      },
      inputInput: {
        padding: theme.spacing(1, 1, 1, 0),
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('md')]: {
          width: '20ch',
        },
      },
    highlight:
      theme.palette.type === 'light'
        ? {
            color: theme.palette.secondary.main,
            backgroundColor: lighten(theme.palette.secondary.light, 0.85),
          }
        : {
            color: theme.palette.text.primary,
            backgroundColor: theme.palette.secondary.dark,
          },
    title: {
      flex: '1 1 100%',
    },
  }));
export default function Search(props){

    const classes = useToolbarStyles();
    const [email,setEmail]=useState("")
    const [batchno,setBatchNo]=useState("")
    const { setUserList,userList,setTempInfo } = props;
    useEffect(
      ()=>{
        search()
      },
    [props.search])
    const search=()=>{
        if(email&&batchno){
          let temp=userList.filter((item,index)=>{
            return item.email.includes(email)&&item.batchno.includes(batchno)
          })
          setUserList(temp)
          setTempInfo&&setTempInfo(temp)
        }else if(email&&!batchno){
          let temp=userList.filter((item,index)=>{
            return item.email.includes(email)
          })
          setUserList(temp)
          setTempInfo&&setTempInfo(temp)
        }else if(!email&&batchno){
          let temp=userList.filter((item,index)=>{
            return item.batchno.includes(batchno)
          })
          setUserList(temp)
          setTempInfo&&setTempInfo(temp)
        }else{
          setUserList(userList)
          setTempInfo&&setTempInfo(userList)
        }
      }
      const refresh=()=>{
        setEmail("")
        setBatchNo("")
        setUserList(userList)
      }

    return(
        <div>
        <div className={classes.search}>
            <div className={classes.searchIcon}>
              <SearchIcon />
            </div>
            <InputBase
              placeholder="Search with email.."
              classes={{
                root: classes.inputRoot,
                input: classes.inputInput,
              }}
              inputProps={{ 'aria-label': 'search' }}
              value={email}
              onChange={(e)=>{
                  setEmail(e.target.value)
              }}
            />
        </div>
        <br/>
      <div className={classes.search}>
            <div className={classes.searchIcon}>
              <SearchIcon />
            </div>
            <InputBase
              placeholder="Search with batch no.."
              classes={{
                root: classes.inputRoot,
                input: classes.inputInput,
              }}
              inputProps={{ 'aria-label': 'search' }}
              type="number"
              value={batchno}
              onChange={(e)=>{
                  setBatchNo(e.target.value)
              }}
            />
        </div>
        <br/>
        <Row style={{marginLeft:10}}>
        <Button style={{marginLeft:10}} color="primary" variant="outlined" onClick={()=>{
          search()
        }}>Search</Button>
        <Button style={{marginLeft:10}} color="primary" variant="outlined" onClick={()=>{
          refresh()
        }}>Refresh</Button>
        </Row>
        <br/>
        </div>
    );
}