import React,{useState, useEffect} from 'react'
import { CardImg,Card,CardHeader,CardBody,CardText,CardTitle,Row,Col } from 'reactstrap'
import { Button } from '@material-ui/core'
import {fetchWbjeeQuestion,deleteQuestionWb } from '../../../../firebase/admin/exam/wbjee';

function QuestionWb(props){
    const deleteQuestion=()=>{
        deleteQuestionWb(props.id)
    }
    return(
        <div style={{marginTop:20}}>
            <Card style={{width:"90%"}}>
            {props.imageurl&&<a href={props.imageurl} data-lightbox="photos">
            <CardImg style={{overflow:"auto"}}  src={props.imageurl} 
            style={{cursor:'pointer'}} alt="Card image cap"
            /></a>}
            <CardBody>
                {props.text&&<CardText>{props.text}</CardText>}
                {props.catagory&&<CardText>Catagory: {props.catagory}</CardText>}
                
                {props.answer&&<div>{props.answer.length>0?"Answers":"Answer"}:{props.answer.map((item,index)=>{
                    return(<CardText key={index}>{item}</CardText>)
                })}</div>}
                <Button color="secondary" 
                variant="outlined" onClick={()=>{
                    deleteQuestion()
                }} style={{marginTop:5}}>Delete</Button>
            </CardBody>
            </Card>
        </div>
    );
}

export default function QuestionsWb(props){
    const [data,setData]=useState([])
    const {examcatagory}=props
    useEffect(()=>{
        fetchWbjeeQuestion(examcatagory,setData)
    },[])
    return(
        <div>
            {data.map((item,index)=>{
                return(
                    <QuestionWb
                    key={index}
                    id={item.id}
                    imageurl={item.image} 
                    text={item.text} 
                    catagory={item.catagory}
                    answer={item.answer}
                    />
                );
            })}
        </div>
    );
}