import { firebaseapp } from "../../init"

export const VideoUpload=async(link)=>{
    try{
        await firebaseapp.firestore().collection('videos').add({
            link:link,
            date:Date.now()
        })
        return {
            status:200,
            message:"Successfully uploaded"
        }
    }catch(err){
        return{
            status:400,
            message:"Error Ocuured: "+err.message
        }
    }
    
}