import React,{useEffect, useState} from 'react'
import ProfileImage from './profileimage';
import ParentDetails from './parent'
import AboutProfile from './about'
import MarkSheet from './marksheet';
import { fetchProfile } from '../../../firebase/users/profile/profileFetch';
import { toast } from 'react-toastify';
import ProfileUpdate from './update/updateForm';
import '../../../css/profile.css'
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import { makeStyles } from '@material-ui/core/styles';
import { Button } from '@material-ui/core';
import Cookie from 'js-cookie'
import {useHistory} from 'react-router-dom'

function a11yProps(index) {
    return {
      id: `simple-tab-${index}`,
      'aria-controls': `simple-tabpanel-${index}`,
    };
  }
  
  const useStyles = makeStyles(theme => ({
    root: {
      flexGrow: 1,
      backgroundColor: theme.palette.background.paper,
    },
  }));

function TabPanel(props) {
    const { children, value, index, ...other } = props;
  
    return (
      <Typography
        component="div"
        role="tabpanel"
        hidden={value !== index}
        id={`simple-tabpanel-${index}`}
        aria-labelledby={`simple-tab-${index}`}
        {...other}
      >
        {value === index && <Box p={3}>{children}</Box>}
      </Typography>
    );
  }


const UserProfile=(props)=>{
    const [profile,setProfile]=useState({})
    const [isMarksheetActive,setMarksheetActive]=useState(false)
    const {open,handleOpen,handleClose}=props
    const classes = useStyles();
    const [value, setValue] = React.useState(0);
    const history=useHistory()
    useEffect(()=>{
        fetchUserProfile()
    },[props.uid])
    useEffect(()=>{
        if(Object.entries(profile).length>0)
        {
            if(profile.grantStatus.includes('joint'))
            {
                setMarksheetActive(true)
            }
        }
    },[profile])

    const handleChange = (event, newValue) => {
        setValue(newValue);
      };



    const fetchUserProfile=async()=>{
        console.log(props.uid)
        const resp=await fetchProfile(props.uid)
        if(resp.status==200)
        {
            setProfile(resp?.data)
        }else{
            toast.error(resp?.message)
        }
    }
    
    return(
        <div>
        <ProfileUpdate open={open} handleClose={handleClose} 
        email={profile.email} profile={profile} history={history}
        uid={props.uid}
        fetchUserProfile={fetchUserProfile}/>
        <div className="container emp-profile" style={{marginTop:20}}>
                <div className="row" style={{paddingLeft:10}}>
                    <ProfileImage image={profile.imageurl} fetchUserProfile={fetchUserProfile} 
                    imageurlpath={profile.imageurlpath} email={profile.email}/>
                    <div className="col-md-6">
                        <div className="profile-head">
                                    <h5>
                                        {profile.name?profile.name:"No Record"}
                                    </h5>
                                    <h6>
                                        Student of Asim Roy Chowdhury
                                    </h6> 
                            {/* <ul className="nav nav-tabs" id="myTab" role="tablist">
                                <li className="nav-item">
                                    <a className="nav-link active" style={{color:'rgba(194,21,0,1)'}} id="home-tab" data-toggle="tab" href="#home" role="tab" aria-controls="home" aria-selected="true">About</a>
                                </li>
                                {isMarksheetActive&&<li className="nav-item">
                                    <a className="nav-link" id="profile-tab" style={{color:'rgba(194,21,0,1)'}} data-toggle="tab" href="#profile" role="tab" aria-controls="profile" aria-selected="false">Marksheet</a>
                                </li>}
                            </ul> */}
                            
                        </div>
                    </div>
                </div>
                <div className="row">
                    <ParentDetails 
                        fathername={profile.fathername} 
                        fatheroccupation={profile.fatheroccupation} 
                        fatherphno={profile.fatherphno}
                        mothername={profile.mothername}
                        motheroccupation={profile.motheroccupation}
                        motherphno={profile.motherphno}
                    />
                    <div className="col-md-8">
                    <AppBar position="static" 
                        style={{backgroundColor:"rgb(150, 130, 66)"}}>
                            <Tabs value={value} onChange={handleChange} aria-label="simple tabs example">
                            <Tab label="About" {...a11yProps(0)} color="white"/>
                            {isMarksheetActive&&<Tab label="MarkSheet" {...a11yProps(1)} color="white"/>}
                            </Tabs>
                        </AppBar>
                        <TabPanel value={value} index={0}>
                            <AboutProfile 
                                userid={profile.uid}
                                address={profile.address}
                                batchtime={profile.batchtime}
                                class={profile.class}
                                email={profile.email}
                                newschool={profile.newschool}
                                oldschool={profile.oldschool}
                                phonenumber={profile.phonenumber}
                                subjectcombination={profile.subjectcombination}
                                total10={profile.total10}
                                total12={profile.total12}
                            />
                        </TabPanel>
                        <TabPanel value={value} index={1}>
                            <MarkSheet
                                wbjeeoutof={profile.wbjeeoutof}
                                wbjeescore={profile.wbjeescore}
                                jeeoutof={profile.jeeoutof}
                                jeescore={profile.jeescore}
                            />
                        </TabPanel>
                    </div>
                </div>
        </div>
    </div>
    );
}
export default UserProfile