import React,{useEffect,useContext,useState} from 'react'
import BaseLayout from '../../component/shared/baseLayout';
import { Row, Col,Card, CardBody, CardFooter } from 'reactstrap';
import { fetchVideos,deleteVideoSingle } from '../../firebase/commonUser/videos';
import { AuthContext } from '../../context/authContext';
import { Button,CircularProgress } from '@material-ui/core';
import { toast } from 'react-toastify';

export default function Videoes(){
    const [error,setError]=useState("")
    const [videos,setVideos]=useState([])
    const [auth,_]=useContext(AuthContext)
    const [isLoading,setIsLoading]=useState([])
    useEffect(()=>{
        fetch()
    },[])
    const fetch=async()=>{
        let response=await fetchVideos()
        console.log(response)
        if(response.status==200){
            setVideos(response.data)
        }else{
            if(response.message==="No records found"){
                setVideos([])
            }
            setError(response.message)
        }
    }
    const deleteVideo=async(id)=>{
        setIsLoading([id])
        let res=await deleteVideoSingle(id)
        if(res.status==200){
            toast.success(res.message)
            fetch()
        }else{
            setError(res.message)
        }
        setIsLoading([])
    }
    return(
        <div>
            <BaseLayout/>
            <br/>
            <Row>
                {videos.map((item,index)=>{
                    return(
                        <div className="col-lg-3 col-md-6 col-sm-12">
                        <Card style={{width:320}} key={index}>
                        <CardBody>
                        <iframe height="315" 
                        width="100%"
                        src={`https://www.youtube.com/embed/${item.link}`} 
                        frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" 
                        allowFullScreen></iframe>
                        <p>{new Date(item.date).toString()}</p>
                        </CardBody>
                        {auth.isAdmin&&<CardFooter>
                            {!isLoading.includes(item.id)?<Button color="primary" variant="contained" onClick={()=>{
                                deleteVideo(item.id)
                            }}>Delete video</Button>:<CircularProgress color="primary" style={{fontSize:18}}/>}
                        </CardFooter>}
                        </Card>
                        </div>
                    );
                })}
            </Row>
            {error.length>0&& <p style={{color:"red",fontSize:20}}>{error}</p>}
        </div>
    );
}