import React,{useState, useEffect, useContext} from 'react'
import {Table} from 'reactstrap'
import { fetchAllUsers,deleteUser,updateUser,UpdateExam,updateIsLoggedIn } from '../../../firebase/admin/users/useList'
import { Checkbox } from '@material-ui/core'
import EnhancedToolBar from './enhancedToolBar'
import { toast } from 'react-toastify'
import {CircularProgress} from '@material-ui/core'
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import {FormControl,MenuItem,Select,InputLabel,Button,Tooltip} from '@material-ui/core'
import {AuthContext} from '../../../context/authContext'
import {useHistory} from 'react-router-dom'
import Cookie from 'js-cookie'
import { firebaseapp } from '../../../firebase/init'
function UserListComp(props){
    const [userDeleteArray,setUserDeleteArray]=useState([])
    const [userList,setUserList]=useState([])
    const [error,setError]=useState("")
    const [tempUserList,setTempUserList]=useState([])
    const [loading,setLoading]=useState(false)
    const [open,setOpen]=useState(false)
    const [currentUser,setCurrentUser]=useState({})
    const [updateloading,setUpdateLoading]=useState(false)
    const history=useHistory()
    const [auth,_]=useContext(AuthContext)
    useEffect(()=>{
        fetch()
    },[])
    const fetch=async()=>{
        firebaseapp.firestore().collection('users').onSnapshot((snap)=>{
            if(!snap.empty){
                let temp=snap.docs
                let info=[] 
                for(let i=0;i<temp.length;i++){
                    info.push({id:temp[i].id,...temp[i].data()})
                }
                setUserList(info)
                setTempUserList(info)
            }else{
                setError("No data found")
                setUserList([])
            }
        },(err)=>{
            setError(err.message)
        })
        // if(response.status==200){
        //     setUserList(response.data)
        //     setTempUserList(response.data)
        // }else{
        //     if(response.message==="No user found"){
        //         setUserList([])
        //     }
        //     setError(response.message)
        // }
    }
    const handleChange=(id)=>{
        if(userDeleteArray.includes(id)){
            let temp=userDeleteArray.filter((item,index)=>{
                return item!==id
            })
            setUserDeleteArray(temp)
        }else{
            let temp=[...userDeleteArray,id]
            setUserDeleteArray(temp)
        }
    }

    // const search=(str)=>{
    //     let temp=tempUserList.filter((item,index)=>{
    //         return item.email.includes(str)
    //     })
    //     setUserList(temp)
    // }
    const updateExamenable=async(id,bool)=>{
        let res=await UpdateExam(id,bool)
        if(res.status==200){
            toast.success(res.message)
        }else{
            setError(res.message)   
        }
    }
    const deleteUsers=async()=>{
        setLoading(true)
        setOpen(true)
        try{
            let info=[]
            for(let i=0;i<userDeleteArray.length;i++){
                info.push(deleteUser(userDeleteArray[i]))
            }
            let response=await Promise.all(info)
            let flag=true
            console.log(response)
            if(response.length==userDeleteArray.length){
                for(let i=0;i<response.length;i++){
                    flag=flag && response[i].status==200
                }
                if(flag){
                    toast.success("Successfully deleted All users that you have selected")
                    setUserDeleteArray([])
                    fetch()
                }else{
                    toast.success("Some of your user deleted")
                    setUserDeleteArray([])
                    fetch()
                }
            }else{
                setError(response[response.length-1].message)
            }
        }catch(err){
            setError(err.message)
        }
        setLoading(false)
        setOpen(false)
    }
    const handleUpdate=(id,email,grant)=>{
        setCurrentUser({
            id,
            email,
            grant
        })
        setOpen(true)
    }
    const handleClose=()=>{
        setOpen(false)
    }

    const handleClick=(id)=>{
        Cookie.set("profileuid",id)
        history.push("/userprofile")
    }
    const updateUserInfo=async()=>{
        setUpdateLoading(true)
        let response=await updateUser(currentUser.id,currentUser.grant)
        if(response.status==200){
            toast.success(response.message)
            fetch()
        }else{
            toast.error(response.message)
        }
        setUpdateLoading(false)
    }

    if(!auth.isAdmin){
        return(
            <div>
                <h1>You are not authorized to access the site</h1>
            </div>
        );
    }
    return(
        <div> 
            <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                    <DialogTitle id="form-dialog-title">Update User {currentUser.email}</DialogTitle>
                    <DialogContent>
                    {!loading&&<DialogContentText>
                        You can only update the prefference of the user prefference below.
                    </DialogContentText>}
                    <div style={{padding:20}}>
                            {!loading?<FormControl>
                                <InputLabel id="demo-simple-select-label">Prefference</InputLabel>
                                <Select
                                labelId="demo-simple-select-label"
                                id="demo-simple-select"
                                value={currentUser.grant}
                                onChange={(e)=>{
                                    setCurrentUser({...currentUser,grant:e.target.value})
                                }}
                                >
                                <MenuItem value={"11hs"}>11 HS</MenuItem>
                                <MenuItem value={"12hs"}>12 HS</MenuItem>
                                <MenuItem value={"11joint"}>11 JOINT</MenuItem>
                                <MenuItem value={"12joint"}>12 JOINT</MenuItem>
                                <MenuItem value={"11and12joint"}>11 and 12 JOINT</MenuItem>
                                </Select>
                            </FormControl>:<div><CircularProgress color="secondary" style={{fontSize:20}}/><p style={{marginLeft:10}}>Deleting users....</p></div>}
                        </div>
                    </DialogContent>
                    {!loading&&<DialogActions>
                    <Button onClick={handleClose} color="primary">
                        Cancel
                    </Button>
                    {!updateloading?<Button onClick={updateUserInfo} color="primary">
                        Update
                    </Button>:<CircularProgress color="primary"/>}
                    </DialogActions>}
                </Dialog>    
            <EnhancedToolBar numSelected={userDeleteArray.length} 
            setUserList={setUserList} userList={tempUserList} deleteUsers={deleteUsers}/>
            <Table bordered striped hover>
                <thead>
                    <tr>
                        <th>#</th>
                        <th>Email</th>
                        <th>Is Logged in?</th>
                        <th>Exam enable</th>
                        <th>Preference</th>
                        <th>Batch No.</th>
                        <th>Password</th>
                        <th>Delete <Checkbox checked={userDeleteArray.length==userList.length && userList.length>0} 
                            onChange={()=>{
                                if(userDeleteArray.length==userList.length){
                                    setUserDeleteArray([])
                                }else{
                                    let temp=[]
                                    for(let i=0;i<userList.length;i++){
                                        temp.push(userList[i].id)
                                    }
                                    setUserDeleteArray(temp)
                                }
                                
                            }}
                            color="secondary"
                        ></Checkbox></th>
                        <th>Update</th>
                    </tr>
                </thead>
                <tbody>
                    {userList.length>0&&userList.map((item,index)=>{
                        return(
                            <tr key={index} style={{cursor:"pointer"}}>
                                <td>{index+1}</td>
                                <td onClick={()=>{
                                handleClick(item.id)
                                }}>{item.email}</td>
                                <td><Checkbox checked={item.isLoggedIn} onChange={()=>{
                                    updateIsLoggedIn(item.id,!item.isLoggedIn)
                                }}/></td>
                                <td ><Checkbox checked={item.examenable} onChange={()=>{
                                    updateExamenable(item.id,!item.examenable)
                                }}/></td>
                                <td >{item.grantStatus}</td>
                                <td>{item.batchno}</td>
                                <td>{item.password}</td>
                                <td><Checkbox checked={userDeleteArray.includes(item.id)}
                                    onChange={()=>{
                                        handleChange(item.id)
                                    }}
                                /></td>
                                <td><Tooltip title={`update ${item.email}`}>
                                <Button color="secondary" variant="outlined"  onClick={()=>{
                                    handleUpdate(item.id,item.email,item.grantStatus)
                                }}>Update</Button>
                                </Tooltip></td>
                            </tr>
                        );
                    })}
                </tbody>
            </Table>
            {error!=="" &&<p style={{color:"red",fontSize:25}}>{error}</p>}
        </div>
    );
}

export default UserListComp

