import { firebaseapp } from "./init"
import jscookie from 'js-cookie'
import Axios from "axios"
import { url } from "../url"
// const admin="asim123abc@gmail.com"
export const login=async(email,password)=>{
    return await firebaseapp.auth().signInWithEmailAndPassword(email,password).then((async res=>{
        let uid=res.user.uid
        let email=res.user.email
        let jwt=await res.user.getIdToken()
        jscookie.set('uid',uid)
        jscookie.set('email',email)
        jscookie.set('userjwt',jwt)
        if(uid){
            try{
            let res=await firebaseapp.firestore().collection('users').doc(uid).get()
            if(res.exists){
                if(res.data().isLoggedIn)
                {
                    return {
                        status:400,
                        message:"User already logged in another device"
                    }
                }
            }
        }catch(err){
            return{
                status:400,
                message:err.message
            }
        }
        }
        const authTemp={
            isAdmin:false,
            isLoggedIn:true
        }
        let resp=await Axios.get(url+"/auth/checkJWT/"+jwt).then(res=>{
            return{
                status:res.status,
                data:res.data
            }
        }).catch(err=>{
            return{
                status:400,
                message:err.message
            }
        })
        if(resp.status==200)
        {
            if(resp.data.admin)
            {
                authTemp.isAdmin=true
            }
        }
        try{
            if(!authTemp.isAdmin){
                await firebaseapp.firestore().collection('users').doc(uid).update({
                    isLoggedIn:true
                })
            }
           
        }catch(err){
            return{
                status:400,
                message:err.message
            }
        }
        return {
            status:200,
            user:{
                uid,
                email
            },
            auth:authTemp
        }
    })).catch(err=>{
        return {
            status:500,
            message:err.message
        }
    })
}