import React,{useState, useEffect} from 'react'
import { CardImg,Card,CardHeader,CardBody,CardText,CardTitle,Row,Col } from 'reactstrap'
import { Button } from '@material-ui/core'
import {fetchJeemainQuestion,deleteQuestionJee } from '../../../../firebase/admin/exam/jeemain';

function QuestionJee(props){
    const deleteQuestion=()=>{
        deleteQuestionJee(props.id)
    }
    return(
        <div style={{marginTop:20}}>
            <Card style={{width:"90%"}}>
            {props.imageurl&&<a href={props.imageurl} data-lightbox="photos">
            <CardImg style={{overflow:"auto"}} src={props.imageurl} 
            style={{cursor:'pointer'}} alt="Card image cap"
            /></a>}
            <CardBody>
                {props.text&&
                <CardText>{props.text}</CardText>
                }
                {props.answer&&<div>{props.answer.length>0?"Answers":"Answer"}:{props.answer.map((item,index)=>{
                    return(<CardText key={index}>{item}</CardText>)
                })}</div>}
                <Button color="secondary" variant="outlined"
                style={{marginTop:5}}
                onClick={()=>{
                    deleteQuestion()
                }}
                >Delete</Button>
            </CardBody>
            </Card>
        </div>
    );
}

export default function QuestionsJee(props){
    const [data,setData]=useState([])
    const {examcatagory}=props
    useEffect(()=>{
        fetchJeemainQuestion(examcatagory,setData)
    },[])
    return(
        <div>
            {data.map((item,index)=>{
                return(
                    <QuestionJee
                    key={index}
                    id={item.id}
                    imageurl={item.image} 
                    text={item.text} 
                    answer={item.answer}
                    />
                );
            })}
        </div>
    );
}