import { firebaseapp } from "../../init"

export const fetchVideos=async()=>{
    try{
        let res=await firebaseapp.firestore().collection('videos').get()
        if(!res.empty){
            let val=res.docs
            let info=[]
            for(let i=0;i<val.length;i++){
                info.push({
                    id:val[i].id,
                    ...val[i].data()
                })
            }
            return {
                status:200,
                data:info
            }
        }else{
            return{
                status:400,
                message:"No records found"
            }
        }
    }catch(err){

    }
}
export const deleteVideoSingle=async(id)=>{
    try{
        await firebaseapp.firestore().collection('videos').doc(id).delete()
        return{
            status:200,
            message:"Successfully deleted"
        }
    }catch(Err){
        return{
            status:400,
            message:"Error occured: "+Err.message
        }
    }
}